<template>
    <el-tabs class="m-tabs" v-model="view" @tab-click="changeView">
        <el-tab-pane label="" name="index">
            <template #label>
                <el-icon><Collection /></el-icon>
                <b></b>
                <em class="u-secret"></em>
            </template>
        </el-tab-pane>
        <el-tab-pane label="" name="index" disabled>
            <template #label>
                <el-icon><Collection /></el-icon>
                <b></b>
            </template>
        </el-tab-pane>
        <el-tab-pane label="" name="index" disabled>
            <template #label>
                <el-icon><Collection /></el-icon>
                <b></b>
            </template>
        </el-tab-pane>
        <el-tab-pane label="" name="index" disabled>
            <template #label>
                <el-icon><Collection /></el-icon>
                <b></b>
            </template>
        </el-tab-pane>
        <el-tab-pane label="" name="index" disabled>
            <template #label>
                <el-icon><Collection /></el-icon>
                <b></b>
            </template>
        </el-tab-pane>
        <el-tab-pane label="" name="index" disabled>
            <template #label>
                <el-icon><Collection /></el-icon>
                <b></b>
            </template>
        </el-tab-pane>
    </el-tabs>
</template>

<script>
export default {
    name: "PvpTabs",
    data() {
        return {
            view: "index",
        };
    },
    watch: {
        $route: {
            handler(_route) {
                this.view = _route.name;
            },
            immediate: true,
            deep: true,
        },
    },
    methods: {
        changeView: function () {
            this.$router.push({
                name: this.view,
                query: {
                    subtype: this.$route.query.subtype,
                },
            });
        },
    },
};
</script>

<style lang="less">
@import "@/assets/css/tabs.less";
</style>
